<template>
  <div id="app">
    <!-- Controls -->
    <div style="padding-bottom: 0.25rem">
      <!-- <input type="file" id="file-input" />
      &nbsp; | &nbsp; -->
      <span>Zoom</span>
      &nbsp;
      <input type="text" id="scale-input" />
      &nbsp; | &nbsp;
      <span>Center X </span>
      &nbsp;
      <input type="range" id="x-input" />
      &nbsp;
      <span id="x-output"></span>
      &nbsp; | &nbsp;
      <span>Center Y </span>
      &nbsp;
      <input type="range" id="y-input" />
      &nbsp;
      <span id="y-output"></span>
    </div>

    <!-- Modal -->
    <div v-if="showModal">
      <transition name="modal">
        <div class="modal-mask">
          <div class="modal-wrapper">
            <div class="modal-container">
              <div class="modal-header">Instructions</div>
              <!-- <div class="modal-body">
                Load "pescadero_cores_geo.json" or another geoJSON file to view cores.
                <br /><br />
                Click on the map to re-position the center of the map.
                <br /><br />
                Scroll or type in the "Zoom" text box to zoom in and out.
                <br /><br />
                Move the translation sliders to change the location of the center relative to the container.
              </div> -->
              <div class="modal-body">
                Click on the map to re-position the center of the map.
                <br /><br />
                Scroll or type in the "Zoom" text box to zoom in and out.
                <br /><br />
                Move the translation sliders to change the location of the center relative to the container.
              </div>
              <div class="modal-footer">
                <button @click="showModal = false">OK</button>
              </div>
            </div>
          </div>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import * as d3 from "d3";

export default {
  name: "app",
  data: function () {
    return {
      showModal: true,
      svg: null,
      group: null,
      width: window.innerWidth - 24,
      height: window.innerHeight - 60,
      scale: 120,
      translateX: 0,
      translateY: 0,
      center: [0, 0],
      points: {},
      projection: null,
      path: null,
    };
  },
  mounted: function () {
    let vm = this;

    // put the center in the middle of the screen
    vm.translateX = vm.width / 2;
    vm.translateY = vm.height / 2;

    // add svg element
    vm.svg = d3
      .select("#app")
      .append("svg")
      .attr("id", "svg")
      .attr("width", vm.width)
      .attr("height", vm.height)
      .style("border", "4px solid lightgrey")
      .style("border-radius", "4px");

    // add points group
    vm.group = vm.svg.append("g").attr("id", "group");

    // set the event listeners
    // document.getElementById("file-input").addEventListener("change", vm.readSingleFile, false);
    document.getElementById("scale-input").addEventListener("change", vm.setScale, false);
    document.getElementById("x-input").addEventListener("change", vm.setTranslate, false);
    document.getElementById("y-input").addEventListener("change", vm.setTranslate, false);

    // update scale
    document.getElementById("scale-input").value = vm.scale;

    // update x translation
    document.getElementById("x-input").min = 0;
    document.getElementById("x-input").max = vm.width;
    document.getElementById("x-input").value = vm.translateX;
    document.getElementById("x-output").innerHTML = `${vm.translateX}px`;

    // update y translation
    document.getElementById("y-input").min = 0;
    document.getElementById("y-input").max = vm.height;
    document.getElementById("y-input").value = vm.translateY;
    document.getElementById("y-output").innerHTML = `${vm.translateY}px`;

    // load data
    vm.loadFile("pescadero_cores_geo.json").then(vm.loadData);
  },
  methods: {
    async loadFile(url) {
      const res = await fetch(url);
      return res.text();
    },

    click(e) {
      let vm = this;
      e.preventDefault();
      let dim = document.getElementById("svg").getBoundingClientRect();
      vm.center = vm.projection.invert([e.clientX - dim.left, e.clientY - dim.top]);
      vm.draw(); // re-draw
    },

    zoom(e) {
      let vm = this;
      e.preventDefault();
      const currentScale = vm.scale;
      vm.scale = e.deltaY > 0 ? (vm.scale *= 0.8) : (vm.scale /= 0.8);
      vm.scale = Math.round(vm.scale) < 120 ? 120 : Math.round(vm.scale);
      document.getElementById("scale-input").value = vm.scale;
      if (currentScale !== vm.scale) vm.draw(); // re-draw
    },

    setScale() {
      let vm = this;
      const currentScale = vm.scale;
      const newScale = document.getElementById("scale-input").value;
      vm.scale = Math.round(newScale) < 120 ? 120 : Math.round(newScale);
      document.getElementById("scale-input").value = vm.scale;
      if (currentScale !== vm.scale) vm.draw(); // re-draw
    },

    setTranslate() {
      let vm = this;
      vm.translateX = document.getElementById("x-input").value;
      document.getElementById("x-output").innerHTML = `${vm.translateX}px`;
      vm.translateY = document.getElementById("y-input").value;
      document.getElementById("y-output").innerHTML = `${vm.translateY}px`;
      vm.draw(); // re-draw
    },

    readSingleFile(e) {
      let vm = this;
      var file = e.target.files[0];
      if (!file) {
        return;
      }
      var reader = new FileReader();
      reader.onload = function (e) {
        var contents = e.target.result;
        vm.points = JSON.parse(contents); // store file contents
        vm.draw(); // draw the vis
      };
      reader.readAsText(file);
    },

    loadData(contents) {
      let vm = this;
      vm.points = JSON.parse(contents); // store file contents
      vm.draw(); // draw the vis
    },

    draw() {
      let vm = this;

      // create projection based on current parameters
      vm.projection = d3.geoMercator().center(vm.center).translate([vm.translateX, vm.translateY]).scale(vm.scale);
      vm.path = d3.geoPath().projection(vm.projection);

      // clear previous plot
      vm.group.selectAll("*").remove();

      d3.json("https://enjalot.github.io/wwsd/data/world/world-110m.geojson").then(function (countries) {
        // draw map
        vm.group.selectAll("path").data(countries.features).enter().append("path").attr("d", vm.path);

        // add core sample circles
        if (Object.keys(vm.points).length > 0) {
          vm.group
            .selectAll("circle")
            .data(vm.points.features)
            .enter()
            .append("circle")
            .attr("r", 5)
            .attr("fill", "orange")
            .attr("cx", function (d) {
              return vm.projection(d.geometry.coordinates)[0];
            })
            .attr("cy", function (d) {
              return vm.projection(d.geometry.coordinates)[1];
            });
        }

        // add center cross
        vm.group
          .append("path")
          .attr("transform", `translate(${vm.projection(vm.center)[0]}, ${vm.projection(vm.center)[1]})`)
          .attr("d", d3.symbol(d3.symbolCross))
          .attr("fill", "red");

        // add transparent rect around group to attach event listeners to
        let dim = document.getElementById("group").getBoundingClientRect();
        vm.group
          .append("rect")
          .attr("id", "group-rect")
          .attr("width", dim.width)
          .attr("height", dim.height)
          .attr("transform", `translate(${dim.x - 12}, ${dim.y - 40})`)
          .style("opacity", 0)
          .on("wheel", vm.zoom)
          .on("click", vm.click);
      });
    },
  },
};
</script>

<style scoped>
.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: table;
  transition: opacity 0.3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  width: 450px;
  margin: 0px auto;
  padding: 20px 30px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
  transition: all 0.3s ease;
  font-family: Helvetica, Arial, sans-serif;
}

.modal-header {
  margin-top: 0;
  font-size: 1.2rem;
  font-weight: 800;
  color: #42b983;
}

.modal-body {
  font-size: 0.8rem;
  margin: 20px 0;
}

.modal-footer {
  text-align: right;
}

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
</style>
